<template>
  <el-dialog
      title="mock"
      :visible.sync="visible"
      width="50%"
      :before-close="handleClose"
  >
    <el-form ref="form" :rules="rules" :model="form" label-width="100px">
      <el-form-item label="" prop="mark">
        <el-input v-model="form.mark"></el-input>
      </el-form-item>
      <el-form-item label="mock" prop="url">
        <el-input v-model="form.url"></el-input>
      </el-form-item>
      <el-form-item label="mock" prop="data">
        <el-input
            type="textarea"
            :autosize="{minRows: 4}"
            placeholder=""
            v-model="form.data">
        </el-input>
      </el-form-item>
    </el-form>
    <div slot="footer">
      <el-button @click="handleClose"> </el-button>
      <el-button type="primary" @click="setMockItem"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import { setMockData } from '@/api';
import { mapState, mapMutations } from 'vuex';

export default {
  name: 'createMockItem',
  props: {
    visible: Boolean,
  },
  data() {
    return {
      rules: {
        mark: [{ type: 'string', required: true, message: '', trigger: 'blur' }],
        url: [{ type: 'string', required: true, message: 'mock', trigger: 'blur' }],
        data: [{ type: 'string', required: true, message: 'mock', trigger: 'blur' }],
      },
    };
  },
  computed: {
    ...mapState('Mock', {
      mockItem: state => state.mockItem,
      oldURL: state => state.oldURL,
    }),
    form: {
      get() {
        return this.mockItem;
      },
      set(n) {
        this.SET_MOCK_ITEM(n);
      },
    },
  },
  methods: {
    ...mapMutations('Mock', ['SET_MOCK_ITEM', 'INIT_MOCK_DIALOG']),
    async setMockItem() {
      this.$refs['form'].validate(async valid => {
        if (valid) {
          const row = this.form;
          await setMockData({
            body: {
              ...row,
              data: JSON.parse(row.data),
              oldURL: this.oldURL,
            },
          });
          this.$message.success('mock');

          this.handleClose();
          this.$emit('refresh');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    handleClose() {
      this.$emit('update:visible', false);
      this.INIT_MOCK_DIALOG();
    },
  },
};
</script>

<style scoped>
</style>



